header {
  /* color: #fff;  */
  background-color: #fff; /* Replace with the desired background color */
  color: #000;

  /* New styles for sticky header */
  position: sticky;
  top: 0;
  z-index: 1000; /* Ensures the header stays above other content */
  
}


header .bottom-line {
  height: 4px;
  background-color: #771800;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  padding: 0 20px;
}

nav .logo img {
  width: 150px; /* Adjust as needed */
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

nav ul li {
  display: inline-block;
  margin-right: 20px; /* Adjust spacing as needed */
}

nav ul li a {
  text-decoration: none;
  color: #000;
  transition: color 0.3s;
  font-size: 1rem;
  font-weight: 400;

  display: block;
  padding: 25px 15px;
  position: relative;
}

nav ul li a:hover, nav ul li a:focus {
  color: #fa3200;
}

nav ul li a.clicked:after {
  color: #fa3200;
}

nav ul li {
  position: relative;
}

nav ul li ul {
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  background-color: #000; /* Adjust as needed */
}

nav ul li:hover ul {
  display: block;
}
